import React, { useEffect } from 'react';
import { ActivityIndicator, FlatList, StyleSheet, Text, View } from 'react-native';

import { LMSCourseStatusEnum } from '@constants/enums';
import { useGetAvailableCourseMutation } from '@redux/services';

export default function AvailableCourseScreen() {
  const [getAvailableCourses, { data, isLoading }] = useGetAvailableCourseMutation();

  useEffect(() => {
    getAvailableCourses({
      pageIndex: 0,
      pageSize: 100,
      status: LMSCourseStatusEnum.All,
    });
  }, []);

  return (
    <>
      {isLoading ? (
        <View
          style={styles.container}
        >
          <ActivityIndicator />
        </View>
      ) : (
        <FlatList
          contentContainerStyle={styles.listView}
          data={data?.Data}
          renderItem={({ item }) => (
            <View>
              <Text>
                {item.Title}
              </Text>

              <Text>
                {item.CategoryName}
              </Text>

              <Text>
                {item.Author}
              </Text>
            </View>
          )}
          ItemSeparatorComponent={() => (
            <View style={styles.spacer} />
          )}
          ListEmptyComponent={() => (
            <Text>
              No courses found
            </Text>
          )}
        />
      )}
    </>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  listView: {
    padding: 16,
  },
  spacer: {
    height: 8,
  },
});