import { LMSCourseStatusEnum } from '@constants/enums';
import { IListReponse } from '@constants/types';
import { lmsRoutes } from '@redux/routes';
import { ICourse } from '@redux/types';

import learningApi from './learningApi';

interface ICoursesParam {
  pageIndex: number,
  pageSize: number,
  status: LMSCourseStatusEnum,
}

const learningService = learningApi.injectEndpoints({
  endpoints: (builder) => ({
    getAssignedCourses: builder.mutation<IListReponse<ICourse>, ICoursesParam>({
      query: (body) => ({
        url: lmsRoutes.lmsAssignedCourses,
        body,
        method: 'POST',
      }),
    }),
    getAvailableCourse: builder.mutation<IListReponse<ICourse>, ICoursesParam>({
      query: (body) => ({
        url: lmsRoutes.lmsAvailableCourses,
        body,
        method: 'POST',
      }),
    }),
  }),
  overrideExisting: false,
});

export const {
  useGetAssignedCoursesMutation,
  useGetAvailableCourseMutation,
} = learningService;