import { IApiResponse } from '@constants/types';
import { authRoutes } from '@redux/routes';
import { ILoginDetail } from '@redux/types';

import learningApi from './learningApi';

interface ILoginParam {
  username: string,
  password: string,
}

const authService = learningApi.injectEndpoints({
  endpoints: (builder) => ({
    login: builder.mutation<IApiResponse<ILoginDetail>, ILoginParam>({
      query: (body) => ({
        url: authRoutes.signin,
        body,
        method: 'POST',
      }),
    }),
  }),
  overrideExisting: false,
});

export const {
  useLoginMutation,
} = authService;