import React from 'react';
import { Button, StyleSheet, Text, View } from 'react-native';
import { NativeStackScreenProps } from '@react-navigation/native-stack';
import { logout } from '@rigohr/auth';

import { useAppDispatch } from '@redux/hooks';
import { RootStackParams } from '@routes/RootStack';

type HomeScreenProps = NativeStackScreenProps<RootStackParams, 'Home'>;

export default function HomeScreen({
  navigation,
}: HomeScreenProps) {
  const dispatch = useAppDispatch();

  const onPressLogout = () => {
    dispatch(logout());
  }

  return (
    <View
      style={styles.container}
    >
      <Text>Learning App - Home</Text>

      <Button
        title='Assigned Courses'
        onPress={() => navigation.navigate('AssignedCourse')}
      />

      <Button
        title='Available Courses'
        onPress={() => navigation.navigate('AvailableCourse')}
      />

      <Button
        title='Logout'
        onPress={onPressLogout}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    gap: 16,
  },
});
