import React from 'react';
import { createNativeStackNavigator } from '@react-navigation/native-stack';

import { AssignedCourseScreen, AvailableCourseScreen, HomeScreen } from '@screens/home';

export type RootStackParams = {
  Home: undefined,
  AssignedCourse: undefined,
  AvailableCourse: undefined,
}

const Stack = createNativeStackNavigator<RootStackParams>();

export default function RootStack() {
  return (
    <Stack.Navigator>
      <Stack.Screen
        name='Home'
        component={HomeScreen}
      />

      <Stack.Screen
        name='AssignedCourse'
        component={AssignedCourseScreen}
        options={{
          title: 'Assigned Courses',
        }}
      />

      <Stack.Screen
        name='AvailableCourse'
        component={AvailableCourseScreen}
        options={{
          title: 'Available Courses',
        }}
      />
    </Stack.Navigator>
  );
}